import { TableCell, TableHead, TableRow } from '@material-ui/core';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import React from 'react';

interface TranslationColumnData {
  name: string;
  label: string;
  width: string;
}

export const TRANSLATION_COLUMNS: Array<TranslationColumnData> = [
  {
    name: 'english',
    label: 'English',
    width: '40%',
  },
  {
    name: 'translation',
    label: 'Translation',
    width: '40%',
  },
  {
    name: 'project',
    label: 'Project',
    width: '20%',
  },
];

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
      tableCell: {
        '&:first-child': {
          paddingLeft: theme.spacing(2),
          [theme.breakpoints.up('sm')]: {
            paddingLeft: theme.spacing(3),
          },
        },
        '&:last-child': {
          paddingRight: theme.spacing(2),
          [theme.breakpoints.up('sm')]: {
            paddingRight: theme.spacing(3),
          },
        },
      },
    }),
);

const TranslationTableHead = React.memo(() => {
  const classes = useStyles();
  return (
      <TableHead>
        <TableRow>
          {TRANSLATION_COLUMNS.map(column => (
              <TableCell key={column.name} className={classes.tableCell} style={{ width: column.width }}>
                {column.label}
              </TableCell>
          ))}
        </TableRow>
      </TableHead>
  );
});

export default TranslationTableHead;
