import { IconButton, TableCell, TableRow } from '@material-ui/core';
import { OpenInNewOutlined } from '@material-ui/icons';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import React from 'react';

import { TRANSLATION_COLUMNS } from './TranslationTableHead';

export interface TranslationRowData {
  [key: string]: string;
  englishText: string;
  englishLink: string;
  translationText: string;
  translationLink: string;
  projectText: string;
  projectLink: string;
}

interface Props {
  row: TranslationRowData;
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
      tableCell: {
        '&:first-child': {
          paddingLeft: theme.spacing(2),
          [theme.breakpoints.up('sm')]: {
            paddingLeft: theme.spacing(3),
          },
        },
        '&:last-child': {
          paddingRight: theme.spacing(2),
          [theme.breakpoints.up('sm')]: {
            paddingRight: theme.spacing(3),
          },
        },
        '&:hover $linkButton': {
          opacity: 1,
          pointerEvents: 'auto',
        },
      },
      linkButton: {
        // The following two rules are overriding margin: 0 in .MuiIconButton-root, but because we are rendered in web
        // worker and the stylesheet needs to be prepended, we won't have enough precedence and thus needs !important.
        marginTop: '-12px !important',
        marginBottom: '-12px !important',
        '&:not(:focus)': {
          opacity: 0,
          pointerEvents: 'none',
        },
      },
    }),
);

const TranslationTableRow = React.memo((props: Props) => {
  const classes = useStyles();
  return (
      <TableRow hover key={props.row.translationLink}>
        {TRANSLATION_COLUMNS.map(column => (
            <TableCell key={column.name} className={classes.tableCell} style={{ width: column.width }}>
              {props.row[`${column.name}Text`]}
              <IconButton
                  aria-label='View source' color='inherit' component='a' edge='end'
                  href={props.row[`${column.name}Link`]} target='_blank' rel='noopener noreferrer'
                  className={classes.linkButton}>
                <OpenInNewOutlined />
              </IconButton>
            </TableCell>
        ))}
      </TableRow>
  );
});

export default TranslationTableRow;
