import { createMuiTheme } from '@material-ui/core';
import { Overrides } from '@material-ui/core/styles/overrides';

import { googleBlue, googleGrey } from './Colors';

const overrides: Overrides = {
  MuiMenuItem: {
    root: {
      paddingTop: 12,
      paddingBottom: 12,
    },
  },
  MuiTable: {
    root: {
      tableLayout: 'fixed',
    },
  },
  MuiTableCell: {
    root: {
      overflowWrap: 'break-word',
    },
  },
};

// We need a new theme instance every time we toggle dark theme.
export function createLightTheme() {
  return createMuiTheme({
    palette: {
      primary: {
        main: googleBlue[500],
      },
      secondary: {
        main: googleBlue[500],
      },
      background: {
        default: 'white',
      },
    },
    overrides,
  });
}

export function createDarkTheme() {
  return createMuiTheme({
    palette: {
      primary: {
        main: googleBlue[300],
      },
      secondary: {
        main: googleBlue[300],
      },
      background: {
        default: googleGrey[900],
      },
      type: 'dark',
    },
    overrides,
  });
}
