import parse from 'csv-parse/lib/sync';

import { TranslationRowData } from '../components/TranslationTableRow';

interface AndroidRow {
  sourceText: string,
  sourceFile: string,
  sourceLine: string,
  translationText: string,
  translationFile: string
  translationLine: string,
  projectDirectory: string,
  resourceDirectory: string
}

const columns = {
  'android': [
    'sourceText',
    'sourceFile',
    'sourceLine',
    'translationText',
    'translationFile',
    'translationLine',
    'projectDirectory',
    'resourceDirectory',
  ]
}

export function getRowsFromCsv(csv: string, query: string, superproject: string): Array<TranslationRowData> {
  const lowerCaseQuery = query.toLowerCase();
  switch (superproject) {
    case 'android': {
      const rows = parse(csv, { columns: columns[superproject] }) as Array<AndroidRow>;
      return rows
          .filter(row => row.sourceText.toLowerCase().includes(lowerCaseQuery))
          .slice(0, 1000)
          .map(row => ({
            englishText: row.sourceText,
            englishLink: `https://cs.android.com/android/platform/superproject/+/master:${row.projectDirectory}/${
              row.resourceDirectory}/${row.sourceFile};l=${row.sourceLine}`,
            translationText: row.translationText,
            translationLink: `https://cs.android.com/android/platform/superproject/+/master:${row.projectDirectory}/${
              row.resourceDirectory}/${row.translationFile};l=${row.translationLine}`,
            projectText: row.projectDirectory,
            projectLink: `https://cs.android.com/android/platform/superproject/+/master:${row.projectDirectory}/`,
          }));
    }
    default:
      return [];
  }
}
